.title__menu {
  margin: 20px 0 50px;
}

.title {
  font-family: 'Fira Sans Extra Condensed', sans-serif;
}

.subtitle__menu {
  letter-spacing: 3px;
  line-height: 40px;
}

.subtitle__menu_active {
  font-weight: bold;
  color: #ffffff;
}

.subtitle {
  margin: 0;
}

.paragraph__menu {
  letter-spacing: 3px;
}
.paragraph__link {
  letter-spacing: 6px;
  font-weight: 400;
  padding: 0 10px;
  text-align: center;
  text-decoration: none;
  line-height: 2;
  position: absolute;
  bottom: 8vh;
  right: 50%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border-bottom: 2px solid white;
}

.paragraph {
  margin: 0;
}

.text-color__white {
  color: #ffffff;
}

.text-color__black {
  color: #000000;
}



.text-size__main-link {
  font-size: min(40px, 6vw);
}

.text-size__paragraph-s {
  font-size: 10px;
}

.text-size__subtitle_m {
  font-size: 22px;
}





@font-face {
  font-family: 'Bahnschrift';
  src: local('Bahnschrift'), url(/yi-new-react/static/media/Bahnschrift.7ddf0714.woff) format('woff');
}

@font-face {
  font-family: 'BankGothicMdBT-Medium';
  src: local('BankGothicMdBT-Medium'), url(/yi-new-react/static/media/BankGothicMdBT-Medium.e2bf3a30.woff) format('woff');
}

body {
  margin: 0;
  font-family: 'Fira Sans Extra Condensed', sans-serif;
  font-weight: 200;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Fira Sans Extra Condensed', sans-serif;
  font-weight: 200;
}

.button__menu {
  padding: 0;
  margin: 0;
  width: min(25px, 6vw);
  height: min(16px, 4vw);
  border: none;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABkAAAAPCAYAAAARZmTlAAAAI0lEQVQ4y2P8////f4ZRMAqGLGAcTcKjgHbg////jMMiCQMABGcO9ihXnfgAAAAASUVORK5CYII=);
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.button__lang {
  border: none;
  background: transparent;
}

.button__close-popup {
  width: min(20px, 5vw);
  height: min(20px, 5vw);
  border: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: min(20px, 2vw);
  right: min(20px, 2vw);
  background: transparent;
  background-image: url(/yi-new-react/static/media/close.c852ed24.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
}

.button__back {
  padding: 0;
  margin: 0;
  width: 100vw;
  border: none;
  text-align: center;
  background: transparent;
}

.button__subscribe {
  background: transparent;
  height: calc(1vh * 5);
  height: calc(var(--vh, 1vh) * 5);
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 3px;
}

.button {
  /* сброс стилей */
  text-decoration: none;
  cursor: pointer;
  /* настройки кнопок */
  opacity: 1;
  transition: 0.5s;
  box-sizing: border-box;
}

.button:hover {
  opacity: 0.5;
}

.button:active {
  outline-style: none;
}

.button:focus {
  outline-style: none;
}

.logo__header {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.logo__menu {
  margin-top: min(30px, 5vw);
}

.logo {
  width: min(25px, 6vw);
  height: min(25px, 6vw);
}

.header__icon {
  width: min(20px, 5vw);
  height: min(20px, 5vw);
}

.header__nav {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.header__scrolled {
  background-color: rgba(0, 0, 0, 0.3);
}

.header {
  width: calc(100vw - 20px);
  height: calc(1vh * 8);
  height: calc(var(--vh, 1vh) * 8);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  transition: 1s ease-in-out;
  z-index: 5;
}

.form__container {
  width: min(400px, 70vw);
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  grid-gap: calc(1vh * 2);
  gap: calc(1vh * 2);
  grid-gap: calc(var(--vh, 1vh) * 2);
  gap: calc(var(--vh, 1vh) * 2);
}

.form__paragraph {
  margin-bottom: 20px;
  width: 80%;
  padding: 0 10%;
}

.form {
  height: 75vh;
  height: calc(1vh * 75);
  height: calc(var(--vh, 1vh) * 75);
  display: flex;
  align-items: center;
}

.input__label_blur {
  top: 5px;
}

.input__label_focus {
  top: -20px;
}

.input__label {
  width: 100%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: 0.8s ease-in-out;
}

.input__error {
  font-size: 10px;
  font-weight: normal;
  line-height: 24px;
  height: 24px;
  color: #ff0000;
  margin: 0;
  padding: 0 0 0 5px;
}

.input__field {
  width: 100%;
  background-color: transparent;
  outline: none;
  margin: 0;
  font-size: min(14px, 3vw);
  line-height: 2;
  border: none;
  border-bottom: 1px solid #a0a0a0;
  box-sizing: border-box;
  text-align: center;
}

.input__field:focus {
  box-shadow: 0 0 4px rgba(52, 86, 243, 0.8);
}

.input__field:-webkit-autofill,
.input__field:-webkit-autofill:hover, 
.input__field:-webkit-autofill:focus, 
.input__field:-webkit-autofill:active
{
 -webkit-background-clip: text;
 -webkit-border-image: none;
 font-size: 13px;
} 

.input {
  position: relative;
}

.main__img {
  display: block;
  width: 100vw;
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  object-fit: cover;
}

.main__links {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  grid-gap: calc(1vh * 10);
  gap: calc(1vh * 10);
  grid-gap: calc(var(--vh, 1vh) * 10);
  gap: calc(var(--vh, 1vh) * 10);
}

.main__link {
  padding: 0;
  margin: 0;
  position: relative;
  z-index: 0;
}




















.footer__link {
  text-decoration: none;
  opacity: 0.7;
}

.footer__links {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: calc(1vh * 2);
  gap: calc(1vh * 2);
  grid-gap: calc(var(--vh, 1vh) * 2);
  gap: calc(var(--vh, 1vh) * 2);
}

.footer {
  width: 80vw;
  height: calc(1vh * 25);
  height: calc(var(--vh, 1vh) * 25);
  margin: auto;
  border-top: 1px solid rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup-menu__links {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
}

.popup-menu__link {
  color: #000000;
  text-decoration: none;
}

.popup-menu__lang {
  display: flex;
  justify-content: center;
  grid-gap: min(10px, 2vw);
  gap: min(10px, 2vw);
  padding: 30px 0;
}

.popup-menu__social {
  display: flex;
  justify-content: center;
  grid-gap: min(40px, 10vw);
  gap: min(40px, 10vw);
  margin-bottom: min(30px, 5vw);
}

.popup-menu__icon {
  width: min(30px, 7vw);
  height: min(30px, 7vw);
  /*-webkit-filter: drop-shadow(1px 1px 1px #000);
  filter: drop-shadow(1px 1px 1px #000);*/
}

.popup-menu_opened {
  left: 0;
  right: 0;
}

.popup-menu_closed {
  left: -110%;
  right: 110%;
}

.popup-menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 100vw;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  overflow: auto;
  background-color: #ffffff;
  box-shadow: 7px 0 7px rgba(0, 0, 0, 0.1);
  transition: 0.5s linear;
  text-align: center;
  z-index: 10;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



























